<script>
import store from '@/store/index.js';

export default {
  data: () => ({
    drawer: null,
    overlay: false,
    baseUrl: "https://script.google.com/macros/s/AKfycbxZiEN6WjjWM1k4C6bMrkofZqo_aCfccml-8QIzpC2SNCq-aEwL2SVLBvQ5qAV5CSEAiQ/exec"
  }),

  created: function() {
    store.commit("setBaseUrl", this.baseUrl);
  },
  
  computed: {
    isSignedIn() {
      // return this.$store.state.user !== null;
      return true;
    }
  },
  
  methods: {
    openHome() {
      this.$router.push({ name: "Home" });
      this.drawer = false;
    },
    openCalcBooks() {
      this.$router.push({ name: "CalcBooks" });
      this.drawer = false;
    },
    openCalcWash() {
      this.$router.push({ name: "CalcWash" });
      this.drawer = false;
    },
    openShoppingList() {
      this.$router.push({ name: "ShoppingList" });
      this.drawer = false;
    },
    funcLoad(val) {
      this.overlay = val;
    }
  }
};
</script>

