          >
            追加
            <!--<v-icon>mdi-plus-thick</v-icon>-->
          </v-btn>
        </v-col>
        <v-col cols="4" class="pt-0 pb-0">
          <v-btn
            rounded
            block
            color="red darken-1"
            class="white--text"
            @click="doneShopping()"
          >
            購入
            <!--<v-icon>mdi-plus-thick</v-icon>-->
          </v-btn>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="8">
          <v-btn
            rounded
            block
            disabled
            @click="getHistory()"
          >
            購入履歴
            <!--<v-icon>mdi-plus-thick</v-icon>-->
          </v-btn>
        </v-col>
      </v-row>
    </div>
    <v-dialog
      v-model="isAddPart"
      persistent
    >
      <v-card class="pa-3">
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>買うもの</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addData.item_name"
              dense
              outlined
              hide-details="false"
              autofocus
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>個数</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addData.number"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>メモ</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addData.memo"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>予定店舗</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-select
              v-model="addData.shop_id"
              persistent-hint
              :items="shopList"
              item-text="shop_name"
              item-value="id"
              dense
              hide-details="false"
              outlined
            ></v-select>
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="6">
            <v-btn
              block
              color="primary"
              @click="registNewItem()"
            >登録</v-btn>
          </v-col>
          <v-col cols="6">
            <v-btn
              block
              color="red darken-1"
              class="white--text"
              @click="clearAddData()"
            >入力クリア</v-btn>
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="12">
            <v-btn
              block
              @click="isAddPart=false;clearAddData();"
            >閉じる</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="isEditPart"
      persistent
    >
      <v-card class="pa-3">
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>買うもの</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addData.item_name"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>個数</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addData.number"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>メモ</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addData.memo"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>予定店舗</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-select
              v-model="addData.shop_id"
              persistent-hint
              :items="shopList"
              item-text="shop_name"
              item-value="id"
              dense
              hide-details="false"
              outlined
            ></v-select>
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="6">
            <v-btn
              block
              color="primary"
              @click="updateItem()"
            >更新</v-btn>
          </v-col>
          <v-col cols="6">
            <v-btn
              block
              @click="isEditPart=false;clearAddData();"
            >閉じる</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="isAddShopPart"
      persistent
    >
      <v-card class="pa-3">
        <v-row align="center" class="ma-0" dense>
          <v-col cols="4" class="pr-0">
            <v-subheader>追加店舗名</v-subheader>
          </v-col>
          <v-col cols="8" class="pl-0">
            <v-text-field
              v-model="addShopName"
              dense
              outlined
              hide-details="false"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row align="center">
          <v-col cols="6">
            <v-btn
              block
              color="primary"
              @click="registNewShop()"
            >登録</v-btn>
          </v-col>
          <v-col cols="6">
            <v-btn
              block
              @click="isAddShopPart=false;addShopName='';addData.shop_id='zzz-other';"
            >閉じる</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    
  </div>
