<script>
// import { API } from 'aws-amplify';
// import { listSkmtShopLists, listSkmtItemLists } from '@/graphql/queries';
// import { createSkmtShopList, createSkmtItemList, deleteSkmtItemList, updateSkmtItemList } from '@/graphql/mutations';
import store from '@/store/index.js';

// import draggable from 'vuedraggable';

export default {
  name: "ShoppingList",
  
  components: {
    // draggable
  },
  
  data() {
    return {
      // DBから取得
      shopListDB: [
        // {id: '000', shop_name: '有松ジャンボリー'},
        // {id: '111', shop_name: '店舗111'}
      ],
      
      // DBから取得
      itemListDB: [
        // {id: 'aaa', item_name: 'りんご', number: 1, memo: '', shop_id: 'other'},
        // {id: 'bbb', item_name: 'バナナ', number: 2, memo: 'メモあり', shop_id: 'other'},
        // {id: 'ccc', item_name: 'みかん', number: 1, memo: 'メモあり', shop_id: 'other'}
      ],
      
      emptyStr: '　',
      
      isAddPart: false,
      isEditPart: false,
      addData: {
        id: null,
        item_name: '',
        number: 1,
        memo: '',
        shop_id: 'zzz-other'
      },
      
      isAddShopPart: false,
      addShopName: '',

      baseUrl: null
      
    };
  },
  
  created: async function() {
    this.baseUrl = store.state.baseUrl;
    await this.getShopList();
    await this.getItemList();
  },
  
  computed: {
    
    shopList() {
      console.log('method shopList');
      const list = [
        { id: 'addOpe', shop_name: '店舗追加'},
        { id: 'zzz-other', shop_name: 'その他' }
      ];
      
      const returnList = list.concat(this.shopListDB);
      
      return returnList;
    },
    
    selectedItemList() {
      
      const items = [];
      
      this.itemListDB.forEach(oneItem => {
        const index = items.findIndex(el => el.id == oneItem.shop_id);
        if(index == -1) {
          const shop = this.shopList.find(el => el.id == oneItem.shop_id);
          items.push({
            ...shop,
            item_list: [
              {
                ...oneItem
              }
            ]
          });
        }
        else {
          items[index].item_list.push(
            {
              ...oneItem
            }
          );
        }
      });
      
      items.sort(function(a,b) {
        if(a.id < b.id) return -1;
        if(a.id > b.id) return 1;
        return 0;
      });
      // console.log(items);
      return items;
    }
  },
  
  methods: {
    async fetchData(url, options) {
      let data = null;
      try {
        const response = await fetch(url, options);
        data = await response.json();
      }
      catch(error) {
        console.error("Error:", error);
      }

      return data;
    },

    async getShopList() {
      const url = this.baseUrl + "?keyword=shop_list";
      const result = await this.fetchData(url, {method: "GET"});
      if(result.code == "ok") {
        console.log("getShopList", result);
        result.data.listSkmtShopLists.items.forEach(item => {
          this.shopListDB.push({
            id: item.id,
            shop_name: item.shop_name
          });
        });
      }
    },

    async getItemList() {
      const url = this.baseUrl + "?keyword=item_list";
      const result = await this.fetchData(url, {method: "GET"});
      if(result.code == "ok") {
        console.log("getItemList", result);
        result.data.listSkmtItemLists.items.forEach(item => {
          this.itemListDB.push({
            id: item.id,
            item_name: item.item_name,
            number: item.number,
            memo: item.memo,
            shop_id: item.shop_id,
            checked: false
          });
        });
      }
    },

    async deleteItem(id) {
      const url = this.baseUrl + "?keyword=item_list&command=delete&id=" + id;
      const result = await this.fetchData(url, {method: "GET"});
      console.log(result);
      const index = this.itemListDB.findIndex(el => el.id == id);
      this.itemListDB.splice(index, 1);
    },
    
    clearAddData() {
      this.addData = {
        id: null,
        item_name: '',
        number: 1,
        memo: '',
        shop_id: 'zzz-other'
      };
    },
    
    async doneShopping() {
      console.log('done', this.selectedItemList);
      
      this.selectedItemList.forEach(selectedItem => {
        selectedItem.item_list.forEach(item => {
          if(item.checked) {
            (async() => {
              await this.deleteItem(item.id);
            })();
          }
        });
      });
      
    },
    
    getHistory() {
      
    },

    async registNewItem() {
      if(!this.addData.item_name) {
        return;
      }

      const url = this.baseUrl + "?keyword=item_list&command=regist";
      this.addData.id = crypto.randomUUID();
      const options = {
        method: "POST",
        mode: "no-cors",
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(this.addData)
      };
      const result = await this.fetchData(url, options);
      console.log(result);

      // 前回登録した店舗を表示
      const pre_shop_id = this.addData.shop_id;
      this.itemListDB.push({
        id: this.addData.id,
        item_name: this.addData.item_name,
        number: this.addData.number,
        memo: this.addData.memo,
        shop_id: this.addData.shop_id,
        checked: false
      });
      this.clearAddData();
      this.addData.shop_id = pre_shop_id;
    },

    async registNewShop() {
      
      if(!this.addShopName) {
        return;
      }

      const url = this.baseUrl + "?keyword=shop_list&command=regist";
      const new_shop_data = {
        id: crypto.randomUUID(),
        shop_name: this.addShopName
      };
      const options = {
        method: "POST",
        mode: "no-cors",
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(new_shop_data)
      };
      const result = await this.fetchData(url, options);
      console.log(result);
      
      this.isAddShopPart = false;
      this.addShopName = '';
      this.addData.shop_id = new_shop_data.id;
      this.shopListDB.push(new_shop_data);      
    },
    
    editItem(item) {
      this.isEditPart = true;
      this.addData = item;
    },

    async updateItem() {
      
      if(!this.addData.item_name) {
        return;
      }

      const url = this.baseUrl + "?keyword=item_list&command=update";
      const options = {
        method: "POST",
        mode: "no-cors",
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(this.addData)
      };
      const result = await this.fetchData(url, options);
      console.log(result);
      
      const update_data = Object.assign({}, this.addData);
      this.clearAddData();
      const index = this.itemListDB.findIndex(el => el.id == update_data.id);
      this.itemListDB[index] = {
        id: update_data.id,
        item_name: update_data.item_name,
        number: update_data.number,
        memo: update_data.memo,
        shop_id: update_data.shop_id,
        checked: this.itemListDB[index].checked
      };
      this.itemListDB.splice();
      this.isEditPart = false;
      
    },
    
    checkItem(selectItem) {
      const index = this.itemListDB.findIndex(el => el.id === selectItem.id);
      this.itemListDB.splice(index, 1);
      this.itemListDB.push(selectItem);
    }
    
  },
  
  watch: {
    
    'addData.shop_id': function(val) {
      if(val=='addOpe') {
        this.isAddShopPart = true;
      }
    }
  }
  
};
</script>

