<template>
  <div id="signin-part">
    <amplify-auth-container>
      <amplify-authenticator></amplify-authenticator>
    </amplify-auth-container>
    <div id="button-part">
      <v-btn
        block
        tile
        depressed
        color="transparent"
        @click="forceReload()"
      >キャッシュ削除</v-btn>
    </div>
    <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-text
          style="padding-top: 20px; padding-bottom: 10px;"
        >
          <v-text-field
            v-model="passcode"
            label="Password(数字4桁)"
            type="password"
            ref="password"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            @click="setLogin()"
          >OK</v-btn>
          <!-- <v-btn
            @click="dialog=false"
          >Cancel</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
// import { Auth } from 'aws-amplify';
import store from '@/store/index.js';

export default {
  name: "SignIn",

  data() {
    return {
      dialog: false,
      passcode: null,
    }
  },
  
  created: function() {
    this.dialog = true;
    // window.addEventListener('load', function() {
    //   console.log('-----------');
    //   this.dialog = true;
    // });
  },

  methods: {
    forceReload() {
      window.location.reload(true);
    },

    async setLogin() {
      this.dialog = false;

      // API連携
      // const baseUrl = "https://script.google.com/macros/s/AKfycbzC2cHALHvLj9V8rcbo4MBbtxVrUKUozcM41KhhsH42qorB7ERWVmjVgqJl0uf2sG1BlA/exec";
      const url = store.state.baseUrl + "?keyword=auth&password=" + this.passcode;
      const options = {
        method: "GET"
      };
      const res = await this.fetchData(url, options);

      // ログインOKであれば
      if(res.code == "ok") {
        store.commit("setUser", {"password":this.passcode});
        this.$router.push({name: "Home"});
      }
      else {
        this.passcode = "";
        this.dialog = true;
      }
    },

    async fetchData(url, options) {
      let data = null;
      try {
        const response = await fetch(url, options);
        data = await response.json();
      }
      catch(error) {
        console.error("Error:", error);
      }

      return data;
    }

  },

  watch: {
    dialog: function(val) {
      if(val) {
        setTimeout(() => {
          this.$refs.password.focus();
        }, 200);
      }
    }
  }
};
</script>

<style>
  #signin-part {
    position: relative;
    height: 100vh;
  }
  #button-part {
    position: absolute;
    background-color: #EF5350;
    top: calc(896px - 100px);
    bottom: 0;
    left: 0;
    right: 0;
  }
</style>