import Vue from 'vue';
import VueRouter from 'vue-router';

import store from '@/store/index.js';
// import Auth from '@aws-amplify/auth';
// import {AuthState, onAuthUIStateChange} from '@aws-amplify/ui-components';

import SignIn from '../views/SignIn.vue';
import Home from '../views/Home.vue';
// import CalcWash from '../views/work/CalcWash';
import CalcBooks from '../views/work/CalcBooks';
import ShoppingList from '../views/work/ShoppingList';

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: { name: "Home" }
  },
  {
    path: "/signin",
    //redirect: { name: "Home"},
    name: "SignIn",
    component: SignIn
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
    meta: { requireAuth: true }
  },
  // {
  //   path: "/calc-wash",
  //   name: "CalcWash",
  //   component: CalcWash,
  //   meta: { requireAuth: true }
  // },
  {
    path: "/calc-books",
    name: "CalcBooks",
    component: CalcBooks,
    meta: { requireAuth: true }
  },
  {
    path: "/shopping-list",
    name: "ShoppingList",
    component: ShoppingList,
    meta: { requireAuth: true }
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if(savedPosition) {
      return savedPosition;
    }
    else {
      return { x: 0, y: 0 };
    }
  }
});

