import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: null,
    Personal: [],
    baseUrl: null
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
    setBaseUrl(state, baseUrl) {
      state.baseUrl = baseUrl;
    }
  },
  actions: {
    Personal: state => state.Personal
  },
  modules: {
    
  }
});
